body {
  box-sizing: content-box !important; }

*,
*:before,
*:after {
  box-sizing: inherit !important; }

.f-textarea {
  position: relative;
  font-size: inherit;
  text-align: left; }
  .f-textarea *,
  .f-textarea *:before,
  .f-textarea *:after {
    box-sizing: border-box; }
  .f-textarea__inner {
    display: block;
    position: relative; }
  .f-textarea__field {
    color: inherit;
    padding: 1em 0.9em;
    width: 100%;
    border: 0;
    caret-color: #6fcf97;
    font-size: 1em;
    font-family: inherit;
    line-height: 1.5;
    display: block; }
    .f-textarea__field:focus {
      outline: 0; }
    .f-textarea__field.f-textarea__field[disabled] {
      color: silver; }
    .has-error .f-textarea__field {
      caret-color: #e91e63; }
    .f-textarea__field::-moz-selection {
      color: white;
      background: #6fcf97 !important;
      text-shadow: none; }
    .f-textarea__field::selection {
      color: white !important;
      background: #6fcf97 !important;
      text-shadow: none; }
    .has-error .f-textarea__field::-moz-selection {
      color: white;
      background: #e91e63 !important;
      text-shadow: none; }
    .has-error .f-textarea__field::selection {
      color: white !important;
      background: #e91e63 !important;
      text-shadow: none; }
    .f-textarea__field:-webkit-autofill {
      -webkit-text-fill-color: inherit !important; }
    .f-textarea__field[data-field] {
      resize: none; }
  .f-textarea__label {
    user-select: none;
    font-size: 1em; }
  .f-textarea__label-text {
    color: gray; }
    .f-textarea__field[disabled].f-textarea__field[disabled] + .f-textarea__label .f-textarea__label-text {
      color: silver; }
  .f-textarea__indicator {
    pointer-events: none;
    user-select: none;
    color: silver; }
    .f-textarea__indicator--optional {
      font-size: 0.5em; }
    .f-textarea__indicator--required {
      font-size: 1em; }
    .has-error .f-textarea__field:hover ~ .f-textarea__indicator,
    .has-error .f-textarea__field:focus ~ .f-textarea__indicator {
      color: #e91e63 !important; }
    .f-textarea__field[disabled].f-textarea__field[disabled] ~ .f-textarea__indicator {
      color: silver; }
  .f-textarea__message {
    font-size: 0.8em; }
    .f-textarea__message--instruction {
      color: gray; }
    .f-textarea__message--error {
      color: #e91e63; }
      .has-error .f-textarea__message--error::-moz-selection {
        color: white;
        background: #e91e63 !important;
        text-shadow: none; }
      .has-error .f-textarea__message--error::selection {
        color: white !important;
        background: #e91e63 !important;
        text-shadow: none; }
    .f-textarea__message--error + .f-textarea__message--instruction {
      display: none; }
